let text = document.getElementById("introduction");
let style = document.getElementById("style");

const string =
  `/* 你好，我叫dx
  接下来我演示一下我的前端功底
  首先我要准备一个div */ `;

for (let i = 0; i < string.length; i++) {
  setTimeout(() => {
    if(string[i] === "\n") return;
    else{
       text.innerHTML += string[i];
    } 
  }, 500);
  console.log(i);
}
